<template>
  <div class="rankinglist">
    <!-- 排行榜 -->
    <ul class="rankinglist-box" v-if="isActive">
      <li v-for="(item,index) in rankingList" :key="index">
        <a class="clearfix" v-if="item.tracks.length>0" @click="getRankingIdx(index)">
          <div class="panel-img-left fl">
            <img v-lazy="item.coverImgUrl" alt class="auto-img" />
            <span class="timenew">{{item.updateFrequency}}</span>
          </div>
          <div class="panel-img-content fl">
            <div class="sings-box">
              <div
                class="sings one-text"
                v-for="(v,i) in item.tracks"
                :key="i"
              >{{i+1}}.{{v.first}} - {{v.second}}</div>
            </div>
          </div>
          <div class="panel-img-right">
            <i class="panel-img-arrow"></i>
          </div>
        </a>
        <a class="clearfix panel2 fl" v-else @click="getRankingIdx(index)">
          <div class="panel-img-left2">
            <img v-lazy="item.coverImgUrl" alt class="auto-img" />
            <span class="timenew2">{{item.updateFrequency}}</span>
          </div>
        </a>
      </li>
    </ul>
    <div class="rankingDetail-box" v-else>
      <div class="bg-img" :style="{ 'background-image': `url(${rankingListDetail.coverImgUrl})` }">
        <div class="header" @click="back">
          <img src="../assets/images/reback.png" alt class="auto-img" />
        </div>
        <div class="title">{{rankingListDetail.name}}</div>
        <p>{{rankingListDetail.description}}</p>
      </div>
      <ul class="rdlist-item">
        <Songsheetlistitem
          v-for="(item,index) in rankingListDetail.tracks"
          :key="index"
          :item="item"
          :number="index"
          @translate-currentid="$emit('translate-currentid',$event)"
        ></Songsheetlistitem>
      </ul>
    </div>
  </div>
</template>

<script>
import "../assets/less/rankingList.less";
import Songsheetlistitem from "../components/Songsheetlistitem";

export default {
  name: "Rankinglist",
  components: {
    Songsheetlistitem,
  },
  data() {
    return {
      rankingList: [],
      rankingListDetail: [],
      isActive: true,
    };
  },
  created() {
    this.getRankingList();
  },
  methods: {
    back() {
      this.isActive = true;
    },
    //获取排行榜
    getRankingList() {
      this.rankingList = [];

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "https://music.kele8.cn/toplist/detail",
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 200) {
            this.rankingList = result.data.list;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取排行榜的详情
    getRankingDetail(idx) {
      this.rankingListDetail = [];

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "http://musicapi.leanapp.cn/top/list?idx=" + idx,
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 200) {
            this.rankingListDetail = result.data.playlist;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    getRankingIdx(index) {
      //
      switch (index) {
        case 0:
          this.getRankingDetail(3);
          break;
        case 1:
          this.getRankingDetail(0);
          break;
        case 2:
          this.getRankingDetail(2);
          break;
        case 3:
          this.getRankingDetail(1);
          break;
        case 4:
          this.getRankingDetail(2);
          break;
        case 5:
          this.getRankingDetail(4);
          break;
        case 6:
          this.getRankingDetail(5);
          break;
        case 7:
          this.getRankingDetail(6);
          break;
        case 8:
          this.getRankingDetail(7);
          break;
        case 9:
          this.getRankingDetail(22);
          break;
        case 10:
          this.getRankingDetail(8);
          break;
        case 11:
          this.getRankingDetail(0);
          break;
        case 12:
          this.getRankingDetail(23);
          break;
        case 13:
          this.getRankingDetail(3);
          break;
        case 14:
          this.getRankingDetail(5);
          break;
        case 15:
          this.getRankingDetail(6);
          break;
        case 16:
          this.getRankingDetail(21);
          break;
        case 17:
          this.getRankingDetail(7);
          break;
        case 18:
          this.getRankingDetail(8);
          break;
        case 19:
          this.getRankingDetail(10);
          break;
        case 20:
          this.getRankingDetail(9);
          break;
        case 21:
          this.getRankingDetail(20);
          break;
        case 22:
          this.getRankingDetail(3);
          break;
        case 23:
          this.getRankingDetail(2);
          break;
        case 24:
          this.getRankingDetail(19);
          break;
        case 25:
          this.getRankingDetail(22);
          break;
        case 26:
          this.getRankingDetail(22);
          break;
        case 27:
          this.getRankingDetail(22);
          break;
        default:
          break;
      }
      this.isActive = false;
    },
  },
};
</script>

<style lang="less" scoped>
</style>